import React from "react";
import web from "../src/images/about.jpg";
import {NavLink} from "react-router-dom";
import Common from './Common';
const About =() =>{
  return (
    <>
    <Common 
    
    name="About Us"
    btname="Contact Us"
    imgsrc={web}
    visit="/Contact"
    h3="We are the leading Professionalist in the IT sector providing multiple digital service. "
    
    />
    <div>
      <div className="col-10 mt-5 mx-auto text-justify">
        <h5>
          Welcome to our website. We offer a wide range of services to help businesses grow and thrive in today's competitive market. Our services include Web Development, App Development, Android Development, Digital Marketing, Marketing, and Software Development. Our expert team of professionals is dedicated to delivering customized solutions that cater to your unique business needs. Contact us to learn more about how we can help your business succeed.
          <br /><br />
          At our core, we are a team of experts specializing in Web Development, App Development, Android Development, Digital Marketing, Marketing, and Software Development. We understand the unique needs of our clients and work tirelessly to deliver customized solutions that cater to their specific requirements.
          <br /><br />
          Our web development services are designed to create visually appealing and user-friendly websites that are optimized for performance and search engine visibility. Our app development services encompass the entire mobile app development cycle, from concept to launch, ensuring that your app is user-friendly, scalable, and engaging.
          <br /><br />
          Our Android development services cater to the growing demand for mobile apps on the Android platform, delivering feature-rich and scalable solutions that run seamlessly on all Android devices. Our digital marketing services help businesses build a strong online presence, generate leads, and maximize their ROI through result-oriented strategies such as SEO, PPC, social media, and email marketing.
          <br /><br />
          Our marketing services are designed to help businesses reach their target audience, build brand awareness, and drive sales through comprehensive strategies that cover all aspects of marketing, including branding, advertising, and public relations. Finally, our software development services provide businesses with custom solutions that streamline their operations, enhance productivity, and increase efficiency.
          <br /><br />
          We take pride in our commitment to excellence, our attention to detail, and our passion for innovation. We believe that by staying at the forefront of emerging technologies and trends, we can offer our clients the most effective and efficient solutions for their unique needs.
          
          </h5>
      </div>
    </div>
    </>
  )
};

export default About;