import React, { useState } from "react";

const Contact = () => {
  const [data, setData] = useState({
    fullname:'',
    phone:'',
    email:'',
    message:'',
  });

const InputEvent = (event) => {
    const {name, value} = event.target;
    setData((preVal) => {
      return{
        ...preVal,
        [name] : value,
      }
    })
};

  // const formSubmit = (e) =>{
  //     e.preventDefault();
  //     alert(`Name: ${data.fullname}, Email: ${data.email},Phone: ${data.phone}, Message: ${data.message} `)
  // }
  return (
    <>
      <div className="my-5">
        <h1 className="text-center">Contact US</h1>
        <div className="container contact_div">
          <div className="row">
            <div className="col-md-6 col-10 mx-auto">
              <form action="https://formspree.io/f/mdovplkj" method="POST" >
                <div class="mb-3">
                  <label for="exampleFormControlInput1" class="form-label">
                    Full Name
                  </label>
                  <input
                    type="text"
                    class="form-control"
                    id="exampleFormControlInput1"
                    name="fullname"
                    value={data.fullname}
                    onChange={InputEvent}
                    placeholder="Enter your name"
                    required
                  />
                </div>
                <div class="mb-3">
                  <label for="exampleFormControlInput1" class="form-label">
                    Phone
                  </label>
                  <input
                    type="tele"
                    class="form-control"
                    id="Phone"
                    name="phone"
                    value={data.phone}
                    onChange={InputEvent}
                    placeholder="Mobile number"
                    required
                  />
                </div>
                <div class="mb-3">
                  <label for="exampleFormControlInput1" class="form-label">
                    Email address
                  </label>
                  <input
                    type="email"
                    class="form-control"
                    id="exampleFormControlInput1"
                    name="email"
                    value={data.email}
                    onChange={InputEvent}
                    placeholder="name@example.com"
                    required
                  />
                </div>
                <div class="mb-3">
                  <label for="exampleFormControlTextarea1" class="form-label">
                    Message/ Requirement
                  </label>
                  <textarea
                    class="form-control"
                    id="exampleFormControlTextarea1"
                    name="message"
                    value={data.message}
                    onChange={InputEvent}
                    rows="3"
                    required
                  ></textarea>
                  <div class="col-12">
    <button className=" my-2 btn btn-outline-primary" type="submit">Send</button>
  </div>
      <h6>Once the message is sent we will connect to you ASAP🙏</h6>
  
                </div>
              </form>
            </div>
            <iframe src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d14912.575845431655!2d74.7770103!3d20.8662469!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x3bdec78026840865%3A0xec478c5c964e243d!2sShantanu%20Sawant!5e0!3m2!1sen!2sin!4v1681286205936!5m2!1sen!2sin" width="600" height="450" style={{border:0}}  allowFullScreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
          </div>
          
        </div>
      </div>
    </>
  );
};

export default Contact;
