import React from "react";
import web from "../src/images/1.jpg";
import {NavLink} from "react-router-dom";

const Common =(props) =>{
  return (
    <>
    <section id="header" className=" d-flex align-items-center">
    <div className="container-fluid  ">
            <div className="row  ">
                <div className="col-10 mt-5 mx-auto  ">
                   <div className="row d-flex align-items-center " >
                      <div className="col-md-5 pt-5 pt-lg-0 order-2 order-lg-1 d-flex flex-column ">
                            <h1 className="d-flex flex-column ">
                              {props.name}
                              <strong className="hnm"> Shantanu<span className="creations">Creations</span></strong>
                              </h1>
                            <h3 className="my-3">{props.h3}</h3>
                            <div className="mt-3">
                                  <NavLink className="btn-get-started" to={props.visit}>{props.btname}</NavLink>
                            </div>
                      </div>
                      <div className="col-lg-7 order-1 order-lg-2 header-img">
                              <img src={props.imgsrc} alt="home"  className="img-fluid animated " />
                     
                              </div>
                      </div>
                      
                </div>

            </div>

        </div>
    </section>
    </>
  )
};

export default Common;