import React from "react";
import web from "../src/images/1.jpg";
import {NavLink} from "react-router-dom";
import Common from './Common';
const Home =() =>{
  return (
    <>
    <Common 
      name="Grow Your Business With"
      imgsrc={web}
      visit="/service"
      btname="Services"
      h3=""
    
    />

    <div>
      <div className="col-10 mt-5 mx-auto text-justify">
        <h5>Welcome to our website. We offer a wide range of services to help businesses grow and thrive in today's competitive market. Our services include Web Development, App Development, Android Development, Digital Marketing, Marketing, and Software Development. Our expert team of professionals is dedicated to delivering customized solutions that cater to your unique business needs. Contact us to learn more about how we can help your business succeed.</h5>
      </div>
    </div>
    </>
  )
};

export default Home;